___CSS_LOADER_EXPORT___.push([module.id, `/* stylesheet for to-do task */

body {
    height: 98vh;
    width: 98vw;
    display: grid;
    grid-template-rows: 50px minmax(500px, 1fr) 30px;
    text-align: center;
    font-family: 'Courier New', Courier, monospace;
    font-weight: bolder;
    gap: 2em;
    background-color: azure;
}

h1 {
    display: inline;
    text-shadow: #777;
    text-decoration-line: line-through;
}

header {
    border-bottom: 2px dashed;
}

#addTask {
    margin-bottom: 30px;
    width: 10%;
    font: inherit;
    background-color: azure;
    transition: .5s;
}

#addTask::before {
    content: '';
    margin-bottom: 30px;
    width: 10%;
    font: inherit;
    background-color: azure;
    transition: .5s;
    transform: scale(.9);
    z-index: -1;
}

#addTask:hover::before {
    transform: scale(1.1);
    background-color: black;
    color: azure;
}

#addTask:hover {
    color: azure;
    background-color: black;
    text-shadow: 0 0 5px azure;
}

form {
    display: flex;
    gap: 2em;
    justify-content: center;
    align-items: flex-end;
    border: 2px dotted;
}

input, select {
    font: inherit;
    height: 2em;
    font-size: large;
    font-weight: bolder;
    text-align: center;
    border: none;
    background-color: azure;
}

input, select {
    border-bottom: 1px solid lightgrey;
}

input[name='task'] {
    flex: 3;
    min-width: 300px;
}

input:disabled, select:disabled {
    background-color: azure;
    color: black;
    border: none;
}

select:invalid, input[type='datetime-local']:invalid {
    color: #777;
}

img {
    width: 15px;
    height: auto;
}

button[class='save'], button[class='edit'], button[class='delete'] {
    border: none;
    width: 25px;
    height: 25px;
    background-color: azure;
}

#tasksContainer {
    display: grid;
    justify-content: center;
    gap: 2em;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA,8BAA8B;;AAE9B;IACI,YAAY;IACZ,WAAW;IACX,aAAa;IACb,gDAAgD;IAChD,kBAAkB;IAClB,8CAA8C;IAC9C,mBAAmB;IACnB,QAAQ;IACR,uBAAuB;AAC3B;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,kCAAkC;AACtC;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,mBAAmB;IACnB,UAAU;IACV,aAAa;IACb,uBAAuB;IACvB,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,mBAAmB;IACnB,UAAU;IACV,aAAa;IACb,uBAAuB;IACvB,eAAe;IACf,oBAAoB;IACpB,WAAW;AACf;;AAEA;IACI,qBAAqB;IACrB,uBAAuB;IACvB,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,uBAAuB;IACvB,0BAA0B;AAC9B;;AAEA;IACI,aAAa;IACb,QAAQ;IACR,uBAAuB;IACvB,qBAAqB;IACrB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,WAAW;IACX,gBAAgB;IAChB,mBAAmB;IACnB,kBAAkB;IAClB,YAAY;IACZ,uBAAuB;AAC3B;;AAEA;IACI,kCAAkC;AACtC;;AAEA;IACI,OAAO;IACP,gBAAgB;AACpB;;AAEA;IACI,uBAAuB;IACvB,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,QAAQ;AACZ","sourcesContent":["/* stylesheet for to-do task */\n\nbody {\n    height: 98vh;\n    width: 98vw;\n    display: grid;\n    grid-template-rows: 50px minmax(500px, 1fr) 30px;\n    text-align: center;\n    font-family: 'Courier New', Courier, monospace;\n    font-weight: bolder;\n    gap: 2em;\n    background-color: azure;\n}\n\nh1 {\n    display: inline;\n    text-shadow: #777;\n    text-decoration-line: line-through;\n}\n\nheader {\n    border-bottom: 2px dashed;\n}\n\n#addTask {\n    margin-bottom: 30px;\n    width: 10%;\n    font: inherit;\n    background-color: azure;\n    transition: .5s;\n}\n\n#addTask::before {\n    content: '';\n    margin-bottom: 30px;\n    width: 10%;\n    font: inherit;\n    background-color: azure;\n    transition: .5s;\n    transform: scale(.9);\n    z-index: -1;\n}\n\n#addTask:hover::before {\n    transform: scale(1.1);\n    background-color: black;\n    color: azure;\n}\n\n#addTask:hover {\n    color: azure;\n    background-color: black;\n    text-shadow: 0 0 5px azure;\n}\n\nform {\n    dis